/* Ensure full viewport coverage */
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	scrollbar-width: none; /* Firefox */
	scroll-behavior: smooth; /* Enable smooth scrolling */
	/* Increased scroll padding to prevent clipping */
	scroll-padding-top: 150px; /* Account for fixed navbar with extra space */
}

/* Base body gradient - simplified for scroll-responsive system */
body {
	background: #000000;
	background-attachment: fixed;
	color: white;
	font-family: "Inter", system-ui, -apple-system, sans-serif;
	overflow-x: hidden;
	line-height: 1.6;
	font-weight: 400;
	min-height: 100vh;
	overscroll-behavior: none;
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
}

/* Current active utilities */

/* Multi-layer overscroll protection with enhanced gradients */
html::before {
	content: "";
	position: fixed;
	top: -200vh;
	left: 0;
	right: 0;
	bottom: -200vh;
	background: #000000;
	z-index: -999;
}

/* Hide scrollbar completely */
::-webkit-scrollbar {
	width: 0px;
	background: transparent;
}

::-webkit-scrollbar-thumb {
	background: transparent;
}

/* Hide scrollbar for Firefox */
html {
	scrollbar-width: none;
}

/* Hide scrollbar for IE and Edge */
body {
	-ms-overflow-style: none;
}

/* Animation for metallic shine effect */
@keyframes shine {
	0% {
		left: -100%;
		transition-property: left;
	}
	100% {
		left: 100%;
		transition-property: left;
	}
}

.animate-shine {
	animation: shine 2s infinite;
}

/* Enhanced selection styling with grayscale */
::-moz-selection {
	background: rgba(100, 100, 100, 0.3);
	color: white;
}
::selection {
	background: rgba(100, 100, 100, 0.3);
	color: white;
}

/* Enhanced typography system */
h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 700;
	letter-spacing: -0.025em;
}
